.center-custom {
    text-align: center;
    margin : 4rem 0 0 0;
}

.table-custom {
    margin: 1em auto;
}

td div input {
    height: 1.75rem; /* 60px */
    width: 1.75rem; /* 60px */
    border: 1px solid;
    text-align: center;
}

.borderLeft-custom {
    border-left: solid;
}

.borderTop-custom {
    border-top: solid;
}

.borderRight-custom {
    border-right: solid;
}

.borderBottom-custom {
    border-bottom: solid;
}

/* Dialog styles */
#myDialog {
    display: block;
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    padding: 0;
    margin: 0;
    background-color: #04103bd8;
    backdrop-filter: blur(3px);
    border: none;
}

.dialogContainerTable{
    display: table;
    height: 100%;
    width: 100%;
    /* margin: 0rem 2rem; */
}

.dialogContainer {
    display: table-cell;
    text-align: center;
    color:white;
    vertical-align: middle;
}

.wtf{
    margin-left: 2rem;
    margin-right: 2rem;

}

/* Background blur when dialog is open */
dialog[open] {
    backdrop-filter: blur(0px);
}

/* Button styles */
dialog button {
    background-color: #00000000;
    color: white;
    border: 1px solid white;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 4px;
    cursor: hand;
}

/* Button hover effect */
dialog button:hover {
    background-color: white;
    color: black;
}